<!-- 我的关注 -->
<template>
  <div class="my-concerns">
    <div class="top-fixed">
      <SearchBar @searchVal="searchVal">
        <span class="edit-btn" @click="isCanEdit = true">编辑</span>
      </SearchBar>
    </div>
    <div class="content">
      <div
        class="clone"
        v-show="cloneData.img"
        :class="[isTreans ? 'opacity0' : '']"
        :style="{
					left: cloneData.left + 'px',
					top: cloneData.top + 'px',
					transform: isTreans ? `translate3d(${cloneData.transX}px, ${cloneData.transY}px, 0)` : ''
				}"
      >
        <img :src="cloneData.img" alt />
        <h5>{{cloneData.title}}</h5>
      </div>
      <div class="mask" v-show="!isCanEdit"></div>
      <section>
        <h4>我的关注</h4>
        <ul style="border-bottom: 1px solid #EAEAEA;" class="myapp-list">
          <vuedraggable v-model="myAppList" :delay="100" v-bind="dragOptions">
            <transition-group type="transition">
              <li
                v-for="(item, index) in myAppList"
                :key="index"
                :class="[
									isCanEdit ? 'showBorder' : '',
									delIndex === index ? 'trans' : '',
									'myapp-item'
								]"
              >
                <span class="add-btn" v-show="isCanEdit" @click="delApp(index)"></span>
                <img :src="item.img" alt />
                <h5>{{item.title}}</h5>
              </li>
            </transition-group>
          </vuedraggable>
        </ul>
      </section>
      <section>
        <h4>更多应用</h4>
        <ul class="more-list">
          <vuedraggable
            v-model="moreAppList"
            :sort="false"
            v-bind="dragOptions"
            :handle="'.del-btn'"
          >
            <transition-group type="transition">
              <li
                v-for="(item, index) in moreAppList"
                :key="index"
                :class="[
									isCanEdit ? 'showBorder' : '',
									index > 3 ? 'margin' : '',
									addIndex === index ? 'trans' : ''
								]"
              >
                <span class="del-btn" v-show="isCanEdit" @click="addApp(index, $event)"></span>
                <img :src="item.img" alt />
                <h5>{{item.title}}</h5>
              </li>
            </transition-group>
          </vuedraggable>
        </ul>
      </section>
    </div>
    <div class="handle-bar" v-show="isCanEdit">
      <div class="cancel btn" @click="isCanEdit = false">取消</div>
      <div class="done btn">完成</div>
    </div>
  </div>
</template>
<script>
import vuedraggable from "vuedraggable";
import SearchBar from "../components/SearchBar";
const rootFontSize =
  document.documentElement.getBoundingClientRect().width / 3.75;
export default {
  name: "MyConcerns",
  components: {
    vuedraggable,
    SearchBar
  },
  data() {
    return {
      delIndex: -1,
      addIndex: -1,
      isTreans: false,
      isCanEdit: false,
      myAppList: [
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "1"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "2"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "3"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "4"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "5"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "6"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "7"
        }
      ],
      moreAppList: [
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "aa"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "bb"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "cc"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "dd"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "ee"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "ff"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "gg"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "hh"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "ii"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "jj"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "kk"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "ll"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "mm"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "nn"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "oo"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "pp"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "qq"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "rr"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "ss"
        },
        {
          img: require("../assets/images/icon_slt_4@2x.png"),
          title: "tt"
        }
      ],
      cloneData: {}
    };
  },
  mounted() {},
  computed: {
    dragOptions() {
      return {
        animation: 300,
        group: "description",
        ghostClass: "ghost"
      };
    }
  },
  methods: {
    searchVal(val) {
      console.log(val);
    },
    // 点击添加按钮
    addApp(index, e) {
      const addItemDom = e.currentTarget.parentNode;
      const newItemDom =
        this.myAppList.length > 0
          ? document.querySelectorAll(".myapp-item")[this.myAppList.length - 1]
          : undefined;
      const cloneItem = this.moreAppList[index];
      const addItemRect = e.currentTarget.parentNode.getBoundingClientRect(); // 元素宽高
      const isNextLine = this.myAppList.length % 4 === 0; // 新加元素是否在新的一行
      const newLeft = newItemDom
        ? isNextLine
          ? 0
          : newItemDom.offsetLeft + addItemRect.width - 1
        : 0; // 新加元素位置
      const newTop = newItemDom
        ? isNextLine
          ? newItemDom.offsetTop + addItemRect.height - 1
          : newItemDom.offsetTop
        : rootFontSize * 0.9; // 新加元素位置
      this.addIndex = index;
      this.cloneData = {
        img: cloneItem.img,
        title: cloneItem.title,
        left: addItemDom.offsetLeft,
        top: addItemDom.offsetTop,
        transX: newLeft - addItemDom.offsetLeft,
        transY: newTop - addItemDom.offsetTop
      };
      setTimeout(() => {
        this.isTreans = true;
      });
      setTimeout(() => {
        this.addIndex = -1;
        this.moreAppList.splice(index, 1);
        this.myAppList.push(cloneItem);
        setTimeout(() => {
          this.cloneData = {};
          this.isTreans = false;
        }, 400);
      }, 400);
    },
    delApp(index) {
      this.delIndex = index;
      const item = this.myAppList[index];
      setTimeout(() => {
        this.delIndex = -1;
        this.myAppList.splice(index, 1);
        this.moreAppList.push(item);
      }, 300);
    }
  }
};
</script>
<style lang="less" scoped>
.my-concerns {
  min-height: 100%;
  background: #fff;
  position: relative;
}
@keyframes opacity0 {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.clone {
  width: 25%;
  height: 0.84rem;
  background: #ffffff;
  padding-top: 0.18rem;
  box-sizing: border-box;
  text-align: center;
  font-size: 0;
  border: 1px solid #eaeaea;
  position: absolute;
  z-index: 9;
  margin-bottom: 1px;
  transition: all 0.4s linear;
  img {
    width: 0.32rem;
    height: 0.32rem;
  }
  h5 {
    font-size: 0.13rem;
    color: #232323;
    margin-top: 0.06rem;
    line-height: 0.18rem;
  }
}
.opacity0 {
  animation: opacity0 0.4s linear forwards;
}
.top-fixed {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
}
.edit-btn {
  width: 0.5rem;
  height: 0.28rem;
  font-size: 0.14rem;
  color: #ffffff;
  background: #45a4b1;
  margin-left: 0.1rem;
  line-height: 0.28rem;
  text-align: center;
  &:active {
    background: rgb(45, 107, 116);
  }
}
.content {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  position: relative;
  .mask {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
  }
  section {
    overflow: hidden;
    &:first-child {
      margin-bottom: 0.2rem;
    }
    h4 {
      height: 0.4rem;
      line-height: 0.4rem;
      text-align: center;
      font-size: 0.16rem;
      color: #3a3a3a;
      font-weight: 500;
    }
    ul {
      overflow: hidden;
      width: calc(100% + 5px);
      li {
        width: 25%;
        height: 0.84rem;
        background: #ffffff;
        padding-top: 0.18rem;
        box-sizing: border-box;
        float: left;
        text-align: center;
        font-size: 0;
        margin-left: -1px;
        margin-bottom: -1px;
        border: 1px solid #fff;
        position: relative;
        img {
          width: 0.32rem;
          height: 0.32rem;
        }
        h5 {
          font-size: 0.13rem;
          color: #232323;
          margin-top: 0.06rem;
          line-height: 0.18rem;
        }
        span {
          width: 0.24rem;
          height: 0.24rem;
          position: absolute;
          right: 0;
          top: 0;
        }
        .add-btn {
          background: url("../assets/images/button_delete@2x.png") no-repeat
            left bottom;
          background-size: 0.19rem 0.19rem;
        }
        .del-btn {
          background: url("../assets/images/button_add_app@2x.png") no-repeat
            left bottom;
          background-size: 0.19rem 0.19rem;
        }
      }
      .ghost {
        opacity: 0;
      }
      @keyframes disappear {
        0% {
          width: 25%;
          opacity: 0;
        }
        20% {
          width: 0;
          opacity: 0;
        }
      }
      .trans {
        animation-name: disappear;
        animation-duration: 2s;
        animation-timing-function: linear;
      }
    }
    .myapp-list {
      .sortable-chosen {
        box-shadow: #666 0px 0px 10px;
        border: 1px solid #fff;
      }
    }
    .more-list {
      li {
        margin-bottom: 0;
      }
      .margin {
        margin-top: -1px;
      }
    }
    .showBorder {
      border: 1px solid #eaeaea;
    }
  }
}
.handle-bar {
  position: fixed;
  width: 100%;
  height: 0.5rem;
  background: #ffffff;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.11);
  bottom: 0;
  display: flex;
  padding: 0 0.1rem;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  .btn {
    font-size: 0.14rem;
    width: 0.58rem;
    height: 0.28rem;
    text-align: center;
    line-height: 0.28rem;
  }
  .cancel {
    color: #45a4b1;
  }
  .done {
    background: #45a4b1;
    color: #fff;
    :active {
      background: rgb(45, 107, 116);
    }
  }
}
</style>